@import url("https://fonts.googleapis.com/css?family=Quicksand:400,600&display=swap");
#app {
  background: #3b3b3b;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-transition: all 200ms cubic-bezier(0, 0, 0.58, 1);
  transition: all 200ms cubic-bezier(0, 0, 0.58, 1);
}
#app.green {
  background: #23a186;
}
#app.green #quote-box {
  border-color: #114d40;
  background: #2ccba9;
}
#app.green #quote-box button#new-quote,
#app.green #quote-box a#tweet-quote {
  background: #66dec4;
  border: 1px solid #1e8c75;
}
#app.green #quote-box button#new-quote:hover,
#app.green #quote-box a#tweet-quote:hover {
  background: #3cd5b4;
}
#app.orange {
  background: #bf8622;
}
#app.orange #quote-box {
  border-color: #684913;
  background: #dca038;
}
#app.orange #quote-box button#new-quote,
#app.orange #quote-box a#tweet-quote {
  background: #e7bf79;
  border: 1px solid #a9771e;
}
#app.orange #quote-box button#new-quote:hover,
#app.orange #quote-box a#tweet-quote:hover {
  background: #dfab4e;
}
#app.red {
  background: #9c3636;
}
#app.red #quote-box {
  border-color: #501c1c;
  background: #bf4646;
}
#app.red #quote-box button#new-quote,
#app.red #quote-box a#tweet-quote {
  background: #d37f7f;
  border: 1px solid #892f2f;
}
#app.red #quote-box button#new-quote:hover,
#app.red #quote-box a#tweet-quote:hover {
  background: #c65959;
}
#app.blue {
  background: #253ba8;
}
#app.blue #quote-box {
  border-color: #131e54;
  background: #2f4ad1;
}
#app.blue #quote-box button#new-quote,
#app.blue #quote-box a#tweet-quote {
  background: #6e81df;
  border: 1px solid #203493;
}
#app.blue #quote-box button#new-quote:hover,
#app.blue #quote-box a#tweet-quote:hover {
