}
#app.purple {
  background: #7a2d7d;
}
#app.purple #quote-box {
  border-color: #311232;
  background: #9f3ba3;
}
#app.purple #quote-box button#new-quote,
#app.purple #quote-box a#tweet-quote {
  background: #c363c7;
  border: 1px solid #68266a;
