  background: #3b3b3b;
}
#app.gray #quote-box {
  border-color: #080808;
  background: #555555;
}
#app.gray #quote-box button#new-quote,
#app.gray #quote-box a#tweet-quote {
  background: #7b7b7b;
  border: 1px solid #2e2e2e;
}
#app.gray #quote-box button#new-quote:hover,
#app.gray #quote-box a#tweet-quote:hover {
