}
#app #quote-box {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  max-width: 800px;
  width: 98%;
  max-height: 500px;
  min-height: 150px;
  height: auto;
  border: 1px solid #2e2e2e;
  border-radius: 10px;
  color: #f7f7f7;
  background: #555555;
  -webkit-transition: all 200ms cubic-bezier(0, 0, 0.58, 1);
  transition: all 200ms cubic-bezier(0, 0, 0.58, 1);
}
#app #quote-box button#new-quote,
#app #quote-box a#tweet-quote {
  position: absolute;
  bottom: 15px;
  left: 15px;
  background: #7b7b7b;
  border: 1px solid #2e2e2e;
  border-radius: 4px;
  padding: 8px 12px;
  text-transform: uppercase;
  color: black;
  font-family: "Quicksand", sans-serif;
  font-size: 1em;
  -webkit-transition: all 200ms cubic-bezier(0, 0, 0.58, 1);
  transition: all 200ms cubic-bezier(0, 0, 0.58, 1);
  cursor: pointer;
}
#app #quote-box button#new-quote:hover,
#app #quote-box a#tweet-quote:hover {
  background: #616161;
}
#app #quote-box a#tweet-quote {
  text-decoration: none;
  right: 15px;
  left: auto;
}
#app #quote-box #quote-content {
  max-height: calc(100% - 140px);
  height: auto;
  position: relative;
  width: calc(100% - 180px);
  margin: 60px 90px 80px;
}
#app #quote-box #quote-content #text {
  font-size: 2em;
  position: relative;
}
#app #quote-box #quote-content #text:before {
  position: absolute;
  content: '"';
  font-size: 5em;
  opacity: 0.2;
  left: -60px;
  top: -50px;
}
#app #quote-box #quote-content #author {
  position: relative;
  text-align: right;
  font-style: italic;
  font-size: 1.2em;
  padding-top: 30px;
}
#app #quote-box #quote-content #author:before {
  content: "- ";
}
